::-webkit-scrollbar {
  width: 0.7rem;
  background-color: #171a1b;
}

::-webkit-scrollbar-thumb {
  border-radius: 0.5rem;
  background: #f66232;
}

html {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*, *::after, *::before {
  box-sizing: inherit;
}

body {
  font-family: "Roboto", sans-serif;
  background-color: #171a1b;
}

h1 {
  font-size: 38px;
}

h2 {
  font-size: 33px;
}

h3 {
  font-size: 25px;
}

h4 {
  font-size: 19px;
}

@media (min-width: 768px) {
  h1 {
    font-size: 38px;
  }

  h2 {
    font-size: 35px;
  }

  h3 {
    font-size: 28px;
  }

  h4 {
    font-size: 21px;
  }
}
h1, h2, h3, h4, p {
  margin: 0;
  padding: 0;
}

p {
  font-size: 20px;
}

img {
  width: 100%;
  height: 100%;
}

figure {
  margin: 0;
}

ul {
  padding: 0;
  margin: 0;
}

li {
  list-style: none;
}

a {
  color: #171a1b;
  text-decoration: none;
}

.container {
  width: 90%;
  max-width: 1200px;
  margin: 0 auto;
}

.titles-sections {
  margin: 1rem auto 2rem;
  width: fit-content;
  font-size: 3rem;
  color: transparent;
  background: linear-gradient(to right, #604266 0%, #f66232);
  font-family: "Dancing Script", cursive;
  background-clip: text;
}
.titles-sections::after {
  content: "";
  display: block;
  margin: 0.5rem auto 0;
  width: 60%;
  height: 0.3rem;
  background: #f66232;
}

.up-button {
  background-color: #f66232;
  position: fixed;
  top: 90%;
  right: 0;
  padding: 0.5rem;
  border-radius: 0.5rem;
  width: 2.5rem;
  cursor: pointer;
}

.fade-up-button {
  display: none;
}

.loading {
  overflow: hidden;
}
.loading::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1000;
  width: 100%;
  height: 100vh;
  background: #171a1b;
}

.spinner {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: inherit;
  display: grid;
  place-content: center;
  z-index: 10001;
}
.spinner img {
  width: 10rem;
  height: 12rem;
  animation: spinn 2s ease-in-out infinite;
}
.spinner p {
  color: #f0e9e9;
  font-size: 2rem;
}

@keyframes spinn {
  0% {
    transform: rotate(360deg);
  }
}
.header {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1000;
  width: 100%;
  background-color: #171a1b;
}

.nav {
  position: relative;
  padding: 1rem 0;
}
.nav a {
  color: #f0e9e9;
}
@media (min-width: 576px) {
  .nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 1.5rem;
  }
}

.nav__logo {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
}
.nav__logo_img {
  flex: 0 0 4rem;
}
.nav__logo_title {
  font-family: "Roboto", sans-serif;
  font-weight: 400;
}
.nav__logo_title span {
  font-family: "Dancing Script", cursive;
}

.menu__responsive {
  display: flex;
  flex-wrap: wrap;
  gap: 0.1rem;
  width: 2.5rem;
  height: 2.5rem;
  margin: 1rem auto 0;
}
@media (min-width: 576px) {
  .menu__responsive {
    margin: 0;
  }
}
@media (min-width: 992px) {
  .menu__responsive {
    display: none;
  }
}
.menu__responsive span {
  display: inline-block;
  width: 1rem;
  height: 1rem;
  border-radius: 0.2rem;
  background-color: #f0e9e9;
}
.menu__responsive:active {
  transform: scale(0.9);
}

.nav__links {
  position: absolute;
  top: 100%;
  left: 25%;
  width: 100%;
  min-height: 100vh;
  background-color: #171a1b;
  background: #171a1b;
  transition: left 0.3s ease-in-out;
}
@media (min-width: 768px) {
  .nav__links {
    left: 60%;
  }
}
@media (min-width: 992px) {
  .nav__links {
    position: initial;
    display: flex;
    justify-content: space-between;
    gap: 0.5rem;
    min-width: fit-content;
    max-width: 30rem;
    min-height: fit-content;
  }
}
.nav__links li {
  font-weight: 700;
  font-family: "Dancing Script", cursive;
  font-size: 1.7rem;
}
.nav__links li a {
  display: block;
  padding: 2rem 0 2rem 1rem;
  border-radius: 2rem 0 2rem 0;
  border-bottom: 0.2rem solid transparent;
  transition: all 0.2s ease-in-out;
}
@media (min-width: 992px) {
  .nav__links li a {
    padding: 0.5rem;
  }
}
.nav__links li a:hover {
  background-color: #f66232;
}
.nav__links_contact {
  border-radius: 0.2rem;
  border: 0.1rem solid #f66232;
  background-color: #2f3233;
}

.active {
  border-bottom: 0.2rem solid #f66232 !important;
}

.fade {
  left: 200%;
}

.hero {
  position: relative;
  width: 100%;
  min-height: 100vh;
  margin-top: 9.5rem;
  background: #171a1b url("../images/bg-home.png") center/cover no-repeat;
}
@media (min-width: 576px) {
  .hero {
    margin-top: 5rem;
  }
}

.hero__content {
  position: absolute;
  bottom: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
  opacity: 0;
  filter: blur(10px);
  animation: fade ease-in-out 15s infinite alternate;
}
.hero__content:nth-child(1) {
  animation-delay: -5;
}
.hero__content:nth-child(2) {
  animation-delay: 5s;
}
@media (min-width: 576px) {
  .hero__content {
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    flex-direction: row;
    justify-content: center;
  }
}
.hero__content-title {
  font-size: 3rem;
  font-family: "Dancing Script", cursive;
  color: transparent;
  background: linear-gradient(to bottom, #f66232 0%, #f0e9e9 100%);
  background-clip: text;
}
@media (min-width: 768px) {
  .hero__content-title {
    font-size: 4rem;
  }
}
.hero__content-img {
  flex: 0 1 20rem;
}

@keyframes fade {
  0%, 75% {
    opacity: 1;
    filter: blur(0);
  }
  50%, 100% {
    filter: blur(25px);
    opacity: 0;
  }
}
.good__container, .decorations__container {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(16rem, 30%));
  gap: 0.5rem;
  justify-content: space-evenly;
}

.decorations {
  margin: 2rem auto;
  padding: 2rem 0;
  min-height: 100vh;
  text-align: center;
}
.deco__card {
  position: relative;
  z-index: 10;
  padding: 1rem;
  color: #f0e9e9;
}
.deco__card::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  z-index: -1;
  width: 100%;
  height: 65%;
  display: block;
  color: #f0e9e9;
  background-color: #2f3233;
  border-radius: 2rem;
  box-shadow: 0 0.2rem 0.1rem #f0e9e9;
}
.deco__card:hover .deco__card-footer-buy {
  filter: opacity(1);
}
.deco__card-header-img {
  width: 15rem;
  height: 15rem;
  margin: auto;
}
.deco__card-header-title {
  margin: 0.5rem 0;
  text-transform: capitalize;
  font-family: "Dancing Script", cursive;
  font-size: 2.5rem;
}
.deco__card-body {
  font-weight: 700;
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}
.deco__card-body-discount {
  color: #f66232;
  text-decoration: line-through;
}
.deco__card-body-price {
  font-size: 2rem;
  font-weight: 900;
}
.deco__card-footer {
  position: absolute;
  bottom: 0;
  right: 0;
}
.deco__card-footer-buy {
  display: inline-block;
  margin-top: 0.5rem;
  padding: 1rem 1.5rem;
  border-radius: 1rem 0 1rem;
  color: #f0e9e9;
  font-weight: 700;
  text-transform: uppercase;
  background: linear-gradient(to right, #f66232 0%, #604266 100%);
  filter: opacity(0);
  transition: all 0.3s ease-in-out;
}

.good {
  min-height: 75vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.good__card {
  position: relative;
  text-align: center;
  border-radius: 1rem;
  overflow: hidden;
  color: #f66232;
}
.good__card:hover .good__card-body {
  opacity: 1;
}
.good__card-header-img {
  width: 15rem;
  height: 15rem;
  margin: auto;
  border-radius: 50%;
  border: 0.1rem solid #604266;
}
.good__card-header-title {
  margin: 1rem 0;
  text-transform: capitalize;
}
.good__card-body {
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
  z-index: 10;
  display: flex;
  flex-direction: column;
  gap: 0.8rem;
  width: 100%;
  height: 100%;
  display: grid;
  place-content: center;
  transition: all 0.3s ease-in-out;
}
.good__card-body::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: -1;
  background-color: #171a1b;
  filter: opacity(0.9);
}
.good__card-body-price {
  font-size: 2.5rem;
  font-weight: 700;
}
.good__card-body-buy {
  display: block;
  margin: auto;
  width: fit-content;
  padding: 1rem 1.8rem;
  font-size: 1.3rem;
  font-weight: 700;
  color: #171a1b;
  border-radius: 1rem 0 1rem;
  background: #f0e9e9;
}

.social {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 75vh;
}

.social__container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  padding: 1rem;
  border-radius: 2rem;
  background: linear-gradient(to right, #f66232 0%, #604266 100%);
}

.social__img {
  flex: 1 1 18rem;
}

.social__info {
  flex: 1 0 50%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 1rem;
  font-weight: 900;
  text-align: center;
  color: #f0e9e9;
}
@media (min-width: 768px) {
  .social__info-title {
    font-size: 3.5rem;
  }
}
.social__info-links {
  display: flex;
  justify-content: center;
  gap: 0.5rem;
}
.social__info-links img {
  width: 2rem;
}
.social__info-links img:hover {
  transform: scale(1.1);
}

.accessories__container {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.accessory__card {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  max-width: 60rem;
  padding: 1rem;
  margin: auto;
  border-radius: 3rem;
  border: 0.4rem double #2f3233;
  text-align: center;
  transition: background 0.3s ease-in-out;
}
.accessory__card:hover {
  background-color: #2f3233;
}
.accessory__card:nth-child(odd) .accessory__card-img {
  order: 1;
}
.accessory__card-img {
  flex: 0 1 20rem;
  height: 25rem;
}
.accessory__card-body {
  flex: 1 1 20rem;
  color: #f66232;
  line-height: 1.5;
}
.accessory__card-body .accessory-title {
  margin-top: 1rem;
  text-transform: capitalize;
}
.accessory__card-body .accessory-characteristic {
  margin: 1rem 0;
  text-transform: capitalize;
  font-weight: 700;
  color: transparent;
  background: linear-gradient(to bottom, #f66232 0%, #f0e9e9 100%);
  background-clip: text;
}
.accessory__card-details-buy {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  align-items: center;
  width: 100%;
  margin-top: 2rem;
  font-weight: 700;
}
.accessory__card-details-buy p:nth-child(1) {
  color: #f66232;
  text-decoration: line-through;
}
.accessory__card-details-buy p:nth-child(2) {
  color: #f0e9e9;
  font-size: 2.5rem;
}
.accessory__card-details-buy a {
  padding: 0.5rem 1rem;
  border-radius: 1rem;
  color: #f0e9e9;
  font-weight: 900;
  font-size: 1.5rem;
  background-color: #f66232;
}

.newsletter {
  margin: 10rem auto;
}
.newsletter p {
  font-size: 1.4rem;
  text-align: center;
  color: #f0e9e9;
  margin: 0.5rem auto 1.5rem;
}

.news__form {
  width: 90%;
  margin: auto;
  padding: 0.5rem;
  background: linear-gradient(to right, #604266 0%, #f66232);
}
.news__form-field {
  display: flex;
  flex-wrap: wrap;
}
.news__form-field input {
  padding: 0.5rem 0;
}
.news__form-field input[type=text] {
  width: 100%;
  max-width: 40rem;
  margin: auto;
  padding: 0.5rem;
  font-weight: 900;
  border: none;
}
.news__form-field input[type=submit] {
  flex: 1;
  font-weight: 900;
  background-color: transparent;
  color: #f0e9e9;
  border: none;
}
.news__form-field input[type=submit]:hover {
  cursor: pointer;
}

.footer {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
  padding: 1rem 0 3rem;
  border-top: 0.1rem solid #2f3233;
}
@media (min-width: 768px) {
  .footer {
    flex-direction: row;
    justify-content: space-between;
  }
}
.footer__img {
  width: 15rem;
}
.footer-titles {
  margin-bottom: 1rem;
  color: #f66232;
}
.footer ul {
  display: grid;
  gap: 1rem;
  text-align: center;
}
.footer a {
  color: #f0e9e9;
}

